// pages/index.js
import React, { useEffect } from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/main-layout-ja';
import Seo from '../components/seo';
import '@glidejs/glide';
import '../js/main';
import { Helmet } from "react-helmet";
import LANG from '../js/lang/en';

//Image
import Top from '../images/bg/top.svg';
import TopSp from '../images/bg/top-sp.svg';
import SlideNext from '../images/icons/slider-next.svg';
import SlidePrevious from '../images/icons/slider-prev.svg';
import Categories from '../images/bg/categories.svg';
import CategoriesSp from '../images/bg/categories-sp.svg';
import TopCicle from '../images/bg/top-circle.svg';
import Twitter from '../images/bg/twitter.svg';
import TwitterSp from '../images/bg/twitter-sp.svg';

//Category Default Image
import CommunityDefaultImage from '../images/thumb/cropped/community.png';
import NewsDefaultImage from '../images/thumb/cropped/news.png';
import PromotionDefaultImage  from '../images/thumb/cropped/promotion_council.png';
import TaskForceDefaultImage  from '../images/thumb/cropped/task_force.png';
import UseCaseDefaultImage  from '../images/thumb/cropped/use_case.png';
import WhitePaperDefaultImage   from '../images/thumb/cropped/white_paper.png';
import EventDefaultImage   from '../images/thumb/cropped/event.png';

const IndexPage = ({ data }) => {
  const currentDate = new Date().toISOString();
  data = data.allMicrocmsPosts.edges.filter(
    ({ node }) => node.public_date_ja && node.public_date_ja <= currentDate
  );
  data.forEach(function(article){
    const date = new Date(article.node.public_date_ja);
    const formattedDate = date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
    const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
    const bodyRemovedHtmlTag = article.node.body_ja ?? article.node.body_en;
    article.node.public_date_formatted = desiredFormat;
    article.node.category_en = LANG.CATEGORIES[article.node.category[0]];
    if (bodyRemovedHtmlTag !== null) {
      const withIllipsis = function (data, maxLength) {
        if (data.length > maxLength) {
          return data.slice(0, maxLength - 3) + '...';
        }

        return data;
      }
      article.node.body_ja = withIllipsis(bodyRemovedHtmlTag.replace(/(<([^>]+)>)/ig, ''), 50);
    }
    const categoriesImage = function (category) {
      switch (category) {
        case 'ホワイトペーパー': return WhitePaperDefaultImage;
        case 'お知らせ': return NewsDefaultImage;
        case 'イベント': return EventDefaultImage;
        case 'コミュニティ': return CommunityDefaultImage;
        case 'ユースケース': return UseCaseDefaultImage;
        case '推進協議会': return PromotionDefaultImage;
        case 'タスクフォース': return TaskForceDefaultImage;
        default: return null;
      }
    }
    article.node.default_image = categoriesImage(article.node.category[0]);
  });

  const whatNew = data.slice(0, 3);
  const allNews = data;

  const TopTwitter = () => {
    useEffect(() => {
      const script = document.createElement('script');
      script.src = 'https://platform.twitter.com/widgets.js';
      script.async = true;
      script.charset = 'utf-8';
      document.body.appendChild(script);
      return () => {
        document.body.removeChild(script);
      };
    }, []);
  
    return (
      <section className="top__twitter">
        <picture>
          <source media="(min-width: 768px)" srcSet={Twitter} /><img className="top__twitter-bg" src={TwitterSp} alt="Twitter" />
        </picture>
        <h2 className="top__twitter-title">Twitter</h2>
        <div className="top__twitter-feed">
          <a
            className="twitter-timeline"
            data-theme="light"
            href="https://twitter.com/TwitterDev?ref_src=twsrc%5Etfw"
          >
            Trusted Webさんのツイート
          </a>
        </div>
      </section>
    );
  };

  return <Layout>
    <section className="top">
      <picture>
        <source media="(min-width: 768px)" srcSet={Top} />
        <img className="top__bg" src={TopSp} alt="Top" />
      </picture>
      <div className="default-max-width sp:pr-0">
        <section className="top__wrapper">
          <h2 className="top__heading">
            <p className="heading heading--no-line heading--sm">デジタル社会における</p>
            <p className="heading heading--no-line">新たな信頼の仕組み<span className="heading heading--no-line heading--sm">が、</span></p>
            <p className="heading heading--no-line">新しい価値<span className="heading heading--no-line heading--sm">の創出へ</span></p>
          </h2>
          <h3 className="heading heading--no-line heading--sub">A new system of trust leads<br className="pc:hidden" /> to the creation of new value</h3>
        </section>
      </div>
      <div className="default-max-width">
        <section className="news">
          <h2 className="heading">新着情報</h2>
          <div className="top__wrapper">
            <ul className="news__list">
            {whatNew.map(edge => {
              const article = edge.node

              return (
                <React.Fragment key={article.id}>
                  <li className="news__item">
                    <div className="news__date-container">
                      <span className="news__date" suppressHydrationWarning={true}>{article.public_date_formatted}</span>
                      <span className="tag tag--blue">{article.category[0]}</span>
                    </div>
                    <a href={ "/news/" + article.id } className="news__title">{ article.title_ja }</a>
                  </li>
                </React.Fragment>
              )
            })}
            </ul>
          </div>
          <a className="button" style={{position:'inherit',zIndex:2}} href="/news/">一覧を見る</a>
        </section>
      </div>
      <section className="slider">
        <picture>
          <source media="(min-width: 768px)" srcSet={Categories} />
          <img className="slider__bg" src={CategoriesSp} alt="Categories" />
        </picture>
        <div className="default-max-width">
          <h2 className="heading">トレンドトピック</h2>
          <div className="js-slider">
            <div className="slider__handles" data-glide-el="controls">
              <button className="slider__button" data-glide-dir="&lt;"><img className="slider__handles-img" src={SlidePrevious} alt="Prev" /></button>
              <button className="slider__button" data-glide-dir="&gt;"><img className="slider__handles-img" src={SlideNext} alt="Next" /></button>
            </div>
            <div data-glide-el="track">
              <ul className="slider__list">
              {allNews.map(edge => {
                const article = edge.node;
                const articleTags = article.tags.map(tag => {
                  return <span className="tag tag--solid mt-2" key={tag}>{tag}</span>
                })
                if (article.is_trend) {
                  return (
                    <React.Fragment key={article.id}>
                      <li className="slider__wrapper slider__wrapper--event">
                        <a className="article" href={ "/news/" + article.id } suppressHydrationWarning={true}><span className="article__date" suppressHydrationWarning={true}>{article.public_date_formatted}</span>
                          <div className="article__image-wrapper">
                            <div className="slider__gradient"></div><img className="article__image" src={article.thumbnail_image !== null ? article.thumbnail_image.url : article.default_image} alt="CATEGORY_HERE" />
                            <p className="slider__text">{article.category[0]}<span>{article.category_en}</span></p>
                          </div>
                          <div className="slider__tags-container">{articleTags}</div>
                          <div className="slider__text-wrapper">
                            <p className="article__title">{article.title_ja}</p>
                            <p className="article__description"> {article.body_ja} </p>
                          </div>
                        </a>
                      </li>
                    </React.Fragment>
                  )
                }
              })}
              </ul>
            </div>
          </div>
        </div>
      </section>
      <section className="contributors">
        <div className="contributors__wrapper">
          <img className="contributors__bg" src={TopCicle} alt="Github Contributors" />
          <h2 className="contributors__title">議論<span>に</span>参加<span>していただける方を</span><br />募集<span>しています</span></h2>
        </div>
        <div className="contributors__wrapper contributors__wrapper--secondary"><img className="contributors__bg" src={TopCicle} alt="Github Contributors" />
          <div className="contributors__container">
            <p className="contributors__text">
              Trusted Webに関するGitHubでは、様々な観点でTrusted Webのアーキテクチャをブラッシュしていただける方
              <br className="sp:hidden" />（積極的に議論に参加していただける方）を募集しています。<br />
              GitHubへの参加にご関心のある方は、以下のフォームにてご連絡ください。<br />
              ※情報が少ない場合、招待の判断ができない可能性があることをあらかじめご理解ください。
            </p>
            <a className="button" href="#">GitHubの議論に参加する</a>
          </div>
        </div>
      </section>
      <div className="default-max-width">
        <section className="contents">
          <h2 className="heading">コンテンツ</h2>
          <ol className="contents__list">
            <li className="contents__item"><a className="contents__link" href="/about/"><span className="contents__number">01</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">Trusted Webとは</h3>
                  <p className="contents__description">Trusted Webの概要については、<br />こちらをご覧ください<br />（一般向け、エンジニア向け）。</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
            <li className="contents__item"><a className="contents__link" href="/use-cases/"><span className="contents__number">02</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">事例（ユースケース）</h3>
                  <p className="contents__description">Trusted Webの考え方を生かした、<br />様々な分野の事例を紹介しています。<br />（例：ヘルスケア、行政、サプライチェーン、人材スキル等）</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
            <li className="contents__item"><a className="contents__link" href="/events"><span className="contents__number">03</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">イベント・コミュニティ</h3>
                  <p className="contents__description">様々なイベント情報や、Trusted Webに関連する<br />コミュニティを掲載しています。<br />是非ご参加ください！</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
            <li className="contents__item"><a className="contents__link" href="/promotion-council/"><span className="contents__number">04</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">Trusted Web推進協議会</h3>
                  <p className="contents__description">有識者とオープンに議論しているのが<br />「推進協議会」です。</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
            <li className="contents__item"><a className="contents__link" href="documents/"><span className="contents__number">05</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">ホワイトペーパー</h3>
                  <p className="contents__description">Trusted Webの考え方について、<br />ホワイトペーパーとして詳細をまとめて公開しています。<br />日本政府は有識者と議論しながら、このホワイトペーパーを更新して、議論のたたき台にしています。</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
            <li className="contents__item"><a className="contents__link" href="/public-offering/"><span className="contents__number">06</span>
                <div className="contents__wrapper">
                  <h3 className="contents__title">ユースケースの公募</h3>
                  <p className="contents__description">R3年度補正の公募は終了しました。</p>
                  <button className="button button--secondary button--sm">詳しく見る</button>
                </div></a></li>
          </ol>
        </section>
      </div>
      <TopTwitter />
    </section>
  </Layout>
}

export const query = graphql`
  {
    allMicrocmsPosts(
      sort: { fields: [public_date_ja, createdAt], order: DESC }
    ) {
      edges {
        node {
          id
          title_ja
          body_ja
          body_en
          public_date_ja
          createdAt
          category
          tags
          is_trend
          thumbnail_image {
            url
            height
            width
          }
        }
      }
    }
  }
`

export default IndexPage

export const Head = () => (
  <Seo title='トップページ' />
)